import React, { useState, useEffect, useMemo } from 'react'
import { connect } from 'react-redux'
import { Box, Typography } from '@mui/material'
import { Toast } from '../../helpers/sweetAlert'
import { fetchComms } from '../../slices/commsSlice'
import { postPosLayouts } from '../../slices/posLayoutsSlice'
import { fetchCommClasses } from '../../slices/commClassesSlice'
import { editPosLayoutDrafts, editPosLayoutDraft, editPosLayoutDraftItem } from '../../slices/posLayoutDraftsSlice'
import { updatePriceMapping } from '../../slices/priceMappingSlice'
import { commClassRequest } from '../../apis'

// const mapIndexToCategorySeq = (list) => {
//   return list.map((item, index) => ({ ...item, categorySeq: index }))
// }

const CommList = ({
  auth,
  comms,
  commClasses,
  posLayouts,
  currentLayoutIndex,
  setCurrentLayoutIndex,
  currentLayoutItemIndex,
  posLayoutDrafts,
  palette,
  postPosLayouts,
  editPosLayoutDrafts,
  editPosLayoutDraft,
  editPosLayoutDraftItem,
  updatePriceMapping,
  fetchComms,
  fetchCommClasses,
}) => {
  const [searchName, setSearchName] = useState('')
  const [searchCategory, setSearchCategory] = useState('')
  const [secondaryCategory, setSecondaryCategory] = useState('')
  const [thirdCategory, setThirdCategory] = useState('')
  const [secondaryCategories, setSecondaryCategories] = useState([])
  const [thirdCategories, setThirdCategories] = useState([])

  const [tagName, setTagName] = useState('')
  const [gridItemName, setGridItemName] = useState('')

  const fetchClasses = async (params) => {
    try {
      const { data } = await commClassRequest.get('', {
        headers: { 'Authorization': 'Bearer ' + auth.authenticated },
        params: {
          limit: 'zero',
          catRank: 1,
          ...params,
        }
      })
      return data.list
    }
    catch (err) { return [] }
  }

  useEffect(() => {
    fetchCommClasses({
      catRank: 1,
    })
  }, [fetchCommClasses])

  useEffect(() => {
    fetchComms({ limit: 15 })
  }, [fetchComms])

  const currentLayout = useMemo(() => {
    return posLayoutDrafts[currentLayoutIndex]
  }, [posLayoutDrafts, currentLayoutIndex])
  const currentLayoutItem = useMemo(() => {
    return posLayoutDrafts[currentLayoutIndex] ? posLayoutDrafts[currentLayoutIndex].stockList[currentLayoutItemIndex] : {}
  }, [posLayoutDrafts, currentLayoutIndex, currentLayoutItemIndex])

  useEffect(() => {
    if (currentLayout) {
      setTagName(currentLayout.categoryName)
      setGridItemName(currentLayoutItem.stockName || '')
    }
  }, [currentLayout, currentLayoutItem])

  // const onSearchClick = e => {
  //   e.preventDefault()
  //   const params = searchName ? { name: searchName, limit: 15 } : { limit: 15 }
  //   fetchComms(params)
  // }

  const onSearchClick = e => {
    e.preventDefault()
    const params = searchName
      ? {
        name: searchName,
        categoryId: thirdCategory || secondaryCategory || searchCategory,
        limit: 15
      } : {
        categoryId: thirdCategory || secondaryCategory || searchCategory,
        limit: 15
      }
    fetchComms(params)
  }

  const onCategoryChange = async (e) => {
    const categoryId = e.target.value
    const params = categoryId
      ? { categoryId, name: searchName, limit: 15 }
      : { name: searchName, limit: 15 }
    setSearchCategory(categoryId)
    setSecondaryCategory('')
    setThirdCategory('')
    fetchComms(params)
    const newSecondaryCategories = await fetchClasses({
      catRank: 2, parent: categoryId
    })
    setThirdCategories([])
    setSecondaryCategories(newSecondaryCategories)
  }

  const onSecondaryCategoryChange = async (e) => {
    const categoryId = e.target.value
    const params = categoryId
      ? { categoryId, name: searchName, limit: 15 }
      : { categoryId: searchCategory, name: searchName, limit: 15 }
    setSecondaryCategory(categoryId)
    setThirdCategory('')
    fetchComms(params)
    const newSecondaryCategories = await fetchClasses({
      catRank: 3, parent: categoryId
    })
    setThirdCategories(newSecondaryCategories)
  }

  const onThirdCategoryChange = e => {
    const categoryId = e.target.value
    const params = categoryId
      ? { categoryId, name: searchName, limit: 15 }
      : { categoryId: secondaryCategory || searchCategory, name: searchName, limit: 15 }
    setThirdCategory(categoryId)
    fetchComms(params)
  }

  const onTagNameChange = e => {
    setTagName(e.target.value)
  }

  const onGridItemNameChange = e => {
    setGridItemName(e.target.value)
  }

  const onNameSubmitClick = () => {
    // const originLayout = posLayoutDrafts[currentLayoutIndex]
    const newLayout = { ...currentLayout, categoryName: tagName }
    editPosLayoutDraft({ id: currentLayout.id, layout: newLayout })
  }

  const onGridItemNameSubmitClick = () => {
    // const originLayout = posLayoutDrafts[currentLayoutIndex]
    // const originItem = currentLayout.stockList[currentLayoutItemIndex]
    if (!currentLayoutItem.stockId || !gridItemName) return
    const newItem = { ...currentLayoutItem, stockName: gridItemName }
    editPosLayoutDraftItem({ id: currentLayout.id, index: currentLayoutItemIndex, item: newItem })
  }

  const onDragStart = e => {
    e.dataTransfer.setData('text/id', e.target.dataset.commId || '')
    e.dataTransfer.setData('text/name', e.target.dataset.commName || '')
    e.dataTransfer.setData('text/price', e.target.dataset.commPrice || '')
    e.dataTransfer.setData('text/backgroundColor', e.target.dataset.commColor || '')
  }

  const onTouchEnd = e => {
    const currentTarget = e.currentTarget
    const targetItem = document.elementFromPoint(e.changedTouches[0].clientX, e.changedTouches[0].clientY)
    const targetItemIndex = targetItem && targetItem.dataset.itemIndex
    if (targetItemIndex) {
      const originItem = currentLayout.stockList[targetItemIndex]
      const stockId = currentTarget.dataset.commId || originItem.stockId
      const priceString = currentTarget.dataset.commPrice
      const price = Number(priceString) || (priceString === '0' ? 0 : originItem.price)
      const newItem = {
        ...originItem,
        stockId,
        price,
        stockName: currentTarget.dataset.commName || originItem.stockName,
        backgroundColor: currentTarget.dataset.commColor || originItem.backgroundColor,
      }
      if (stockId) { updatePriceMapping({ [stockId]: price }) }
      editPosLayoutDraftItem({ id: currentLayout.id, index: targetItemIndex, item: newItem })
    }
  }

  const onRecoveryClick = () => {
    editPosLayoutDrafts(posLayouts)
    setCurrentLayoutIndex(0)
  }

  const onSaveClick = () => {
    postPosLayouts(
      posLayoutDrafts,
      () => {
        Toast.fire({
          icon: 'success',
          title: '',
          target: document.querySelector('.right-col')
        })
      },
      (errorTitle, errorMsg) => {
        Toast.fire({
          icon: 'error',
          title: errorTitle || '...',
          // text: errorMsg || '',
          target: document.querySelector('.right-col')
        })
      },
    )

  }

  const filterString = inputString => {
    return inputString.replaceAll(/[[\]]/g, '')
  }

  const renderedClasses = classes => {
    return classes.map(commClass => (
      <option key={commClass.categoryId} value={commClass.categoryId}>
        {filterString(commClass.categoryDescription)}
      </option>
    ))
  }

  const renderedItems = comms && comms.map(item => {
    return (
      <div
        key={item.stockId}
        title={item.description}
        data-comm-id={item.stockId}
        data-comm-name={item.description}
        data-comm-price={item.price ? item.price.price : 0}
        draggable="true"
        onDragStart={onDragStart}
        onTouchEnd={onTouchEnd}
      >
        <div className="comm-name-sec">
          <Typography noWrap={true} variant="body1" title={item.description}>
            {item.description}
          </Typography>
        </div>
        <div className="comm-price-sec">
          <Typography noWrap={true} variant="body2" title={item.price ? item.price.price : 0}>
            {item.price ? item.price.price : 0}
          </Typography>
        </div>
      </div>
    )
  })

  const renderedColors = Object.keys(palette).map((elKey, index) => {
    return (
      <span
        key={index}
        data-comm-color={palette[elKey]}
        draggable="true"
        onDragStart={onDragStart}
        onTouchEnd={onTouchEnd}
        style={{ backgroundColor: palette[elKey] }}
      ></span>
    )
  }).reverse()

  return (
    <Box component="section" id="comm-list"
      sx={{
        position: 'relative',
        display: 'flex',
        flexDirection: 'column',
        flex: '0 0 100%',
        paddingLeft: '1em',
        paddingRight: '1em',
        minHeight: '600px',
        minWidth: 0,
        '& h5': {
          pt: '1.5rem',
          pb: '0.5rem',
          fontWeight: 'bold',
        },
        '& .comm-classes': {
          paddingBottom: '0.75em',
        },
        '& .comm-classes select': {
          width: '100%',
          color: '#666666',
          borderColor: '#cccccc',
        },
      }}
    >
      <Typography variant="h5">
        
      </Typography>

      <Box component="form" className="comm-search"
        sx={{
          padding: '0 0 0.75em',
          '& input[type="text"]': {
            width: '80%',
            border: '1px solid #c4c4c4',
            borderRight: 'none',
            lineHeight: 1.75,
            borderTopLeftRadius: '0.3em',
            borderBottomLeftRadius: '0.3em',
          },
          '& input[type="submit"]': {
            width: '20%',
            lineHeight: 1.75,
            border: '1px solid #c4c4c4',
            borderLeft: 'none',
            letterSpacing: '0.2em',
            color: 'background.default',
            bgcolor: 'primary.light',
            borderTopRightRadius: '0.3em',
            borderBottomRightRadius: '0.3em',
          },
        }}
      >
        <input
          type="text"
          placeholder=""
          value={searchName}
          onChange={e => setSearchName(e.target.value)}
        />
        <input
          type="submit"
          value=""
          onClick={onSearchClick}
        />
      </Box>

      {/* <Box className="comm-classes" sx={{
        paddingBottom: '1em',
        '& select': {
          width: '100%',
          color: '#666666',
          borderColor: '#cccccc',
        },
      }}>
        <select>
          {renderedClasses}
        </select>
      </Box> */}

      <Box className="comm-classes">
        <select onChange={onCategoryChange}>
          <option value=""></option>
          {renderedClasses(commClasses)}
        </select>
      </Box>

      {(secondaryCategories.length > 0) && (
        <Box className="comm-classes">
          <select onChange={onSecondaryCategoryChange}>
            <option value=""></option>
            {renderedClasses(secondaryCategories)}
          </select>
        </Box>
      )}

      {(thirdCategories.length > 0) && (
        <Box className="comm-classes">
          <select onChange={onThirdCategoryChange}>
            <option value=""></option>
            {renderedClasses(thirdCategories)}
          </select>
        </Box>
      )}

      <Box className="comm-list"
        sx={{
          overflowY: 'auto',
          maxHeight: '400px',
          marginBottom: '2em',
          '& > div': {
            display: 'flex',
            alignItems: 'center',
            userSelect: 'none',
            cursor: 'grab',
            backgroundColor: '#ececec',
            borderBottom: '1px dashed #666666',
            padding: '0.375rem 1rem',
          },
          '& .comm-name-sec': {
            flex: '1 1 auto',
            minWidth: 0,
            paddingRight: '1rem',
          },
          '& .comm-name-sec p': {
            letterSpacing: '0.03rem',
          },
          '& .comm-price-sec p': {
            fontWeight: 'bold',
          },
        }}
      >
        {renderedItems}
      </Box>

      <Box className="control-wrapper"
        sx={{
          flexGrow: 1,
          '& .plat-name-wrapper': {
            userSelect: 'none',
            paddingTop: '0.75em',
          },
          '& .plat-name': {
            border: '1px solid #dddddd',
            borderRadius: '0.2em',
            lineHeight: '1em',
            width: '7.5em',
          },
          '& .name-submit': {
            lineHeight: 1.25,
            border: '1px solid #aaaaaa',
          },
        }}
      >
        <div className="plat-name-wrapper">
          <label>:&nbsp;</label>
          <input
            className="plat-name"
            value={tagName}
            onChange={onTagNameChange}
            type="text" />
          <input
            className="name-submit"
            onClick={onNameSubmitClick}
            type="button"
            value="" />
        </div>
        <div className="plat-name-wrapper">
          <label>:&nbsp;</label>
          <input
            className="plat-name"
            value={gridItemName}
            onChange={onGridItemNameChange}
            type="text" />
          <input
            className="name-submit"
            onClick={onGridItemNameSubmitClick}
            type="button"
            disabled={gridItemName ? false : true}
            value="" />
        </div>
        <Box className="comm-color" sx={{
          textAlign: 'left',
          userSelect: 'none',
          paddingTop: '2em',
          '& span': {
            display: 'inline-block',
            width: '2.25em',
            height: '1.5em',
          },
        }}>
          {renderedColors}
        </Box>
      </Box>

      <Box id="layout-save" sx={{ textAlign: 'center' }}>
        <Box className="btn-wrapper" sx={{
          padding: '1.5em 1.5em',
          '& input': {
            backgroundColor: '#777777',
            borderRadius: '0.3em',
            padding: '0.125em 1.25em',
            color: 'background.default',
            fontWeight: 'bold',
            margin: '0.5em 0.25em',
            border: 'none',
            '&:last-child': {
              bgcolor: 'primary.main',
            },
          },
        }}>
          <input type="button" value="" onClick={onRecoveryClick} />
          <input type="button" value="" onClick={onSaveClick} />
        </Box>
      </Box>

    </Box>
  )
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    comms: state.comms,
    commClasses: state.commClasses,
    posLayouts: state.posLayouts.content,
    posLayoutDrafts: state.posLayoutDrafts,
    palette: state.palette,
  }
}

export default connect(
  mapStateToProps,
  {
    postPosLayouts,
    fetchComms,
    fetchCommClasses,
    editPosLayoutDrafts,
    editPosLayoutDraft,
    editPosLayoutDraftItem,
    updatePriceMapping,
  }
)(CommList)