import React, { useState } from 'react'
import { Box, Grid } from '@mui/material'
import GridInput from './GridInput'
import CommList from './CommList'
import PlatTagList from './PlatTagList'
import withAuth from '../Auth/withAuth'


const TouchPlatformBackstage = () => {
  const [isDeleting, setIsDeleting] = useState(false)
  const [currentLayoutIndex, setCurrentLayoutIndex] = useState(0)
  const [recordedLayoutIndex, setRecordedLayoutIndex] = useState(null)
  const [currentLayoutItemIndex, setCurrentLayoutItemIndex] = useState(0)

  return (
    <Box id="touch-platform-backstage" sx={touchPlatformStyle}>
      <Grid container sx={{
        maxWidth: 1600,
        margin: '0 auto',
        boxShadow: '0px 0px 12px -6px #cccccc',
        bgcolor: 'background.light',
      }}>

        <Grid item xs={12} sm={3} className={`left-col ${isDeleting ? 'deleting' : ''}`}>
          <CommList
            isDeleting={isDeleting}
            currentLayoutIndex={currentLayoutIndex}
            currentLayoutItemIndex={currentLayoutItemIndex}
            setCurrentLayoutIndex={setCurrentLayoutIndex}
            setCurrentLayoutItemIndex={setCurrentLayoutItemIndex}
          />
          <div className="mask-field"></div>
        </Grid>

        <Grid item xs={12} sm={9} className={`right-col ${isDeleting ? 'deleting' : ''}`}>
          <PlatTagList
            isDeleting={isDeleting}
            currentLayoutIndex={currentLayoutIndex}
            recordedLayoutIndex={recordedLayoutIndex}
            setIsDeleting={setIsDeleting}
            setCurrentLayoutIndex={setCurrentLayoutIndex}
            setRecordedLayoutIndex={setRecordedLayoutIndex}
            setCurrentLayoutItemIndex={setCurrentLayoutItemIndex}
          />
          <GridInput
            isDeleting={isDeleting}
            currentLayoutIndex={currentLayoutIndex}
            recordedLayoutIndex={recordedLayoutIndex}
            currentLayoutItemIndex={currentLayoutItemIndex}
            setCurrentLayoutIndex={setCurrentLayoutIndex}
            setRecordedLayoutIndex={setRecordedLayoutIndex}
            setCurrentLayoutItemIndex={setCurrentLayoutItemIndex}
          />
          <div className="mask-field"></div>
        </Grid>
      </Grid>
    </Box>
  )
}

const touchPlatformStyle = {
  bgcolor: 'background.paper',
  userSelect: 'none',
  '& > .row': {
    px: 0,
  },
  '& .left-col': {
    position: 'relative',
    display: 'flex',
    height: 'calc(100vh - 2.5rem)',
    padding: 0,
  },
  '& .right-col': {
    position: 'relative',
    display: 'flex',
    flexDirection: 'column',
    height: '100vh',
    padding: 0,
  },
  '& .mask-field': {
    display: 'none',
  },
  '& .deleting .mask-field': {
    position: 'absolute',
    display: 'block',
    top: 0,
    left: 0,
    width: '100%',
    height: 'calc(100vh - 2.5rem)',
    zIndex: 100,
    bgcolor: '#ffeae0a8',
  },
  '@media (min-width: 600px)': {
    '& .right-col': {
      height: 'calc(100vh - 2.5rem)',
    },
  }
}

export default withAuth()(TouchPlatformBackstage)