import React, { useEffect, useMemo } from 'react'
import { connect } from 'react-redux'
import { Box, Grid, Typography } from '@mui/material'
import { editPreviousPalette } from '../../slices/paletteSlice'
import { editPosLayoutDrafts, editPosLayoutDraftItem, clearPosLayoutDraftItem } from '../../slices/posLayoutDraftsSlice'
import { fetchPosLayouts } from '../../slices/posLayoutsSlice'
import { updatePriceMapping, fetchPriceMapping } from '../../slices/priceMappingSlice'

const GridInput = ({
  isDeleting,
  posLayouts,
  posLayoutsStatus,
  posLayoutDrafts,
  priceMapping,
  updatePriceMapping,
  fetchPosLayouts,
  fetchPriceMapping,
  currentLayoutIndex,
  recordedLayoutIndex,
  currentLayoutItemIndex,
  setCurrentLayoutIndex,
  setRecordedLayoutIndex,
  setCurrentLayoutItemIndex,
  editPosLayoutDrafts,
  editPosLayoutDraftItem,
  clearPosLayoutDraftItem,
  editPreviousPalette,
}) => {

  // 1) 
  useEffect(() => {
    fetchPosLayouts()
  }, [fetchPosLayouts])

  // 2) 
  useEffect(() => {
    if (posLayoutsStatus === 'idle') {
      editPosLayoutDrafts(posLayouts)
    }
  }, [editPosLayoutDrafts, setCurrentLayoutIndex, posLayouts, posLayoutsStatus])

  const posLayoutDraft = useMemo(() => {
    return posLayoutDrafts[currentLayoutIndex] || {}
  }, [currentLayoutIndex, posLayoutDrafts])

  // 3) 
  // 
  useEffect(() => {
    if (isDeleting) return
    if (posLayoutDraft.stockList && (recordedLayoutIndex !== currentLayoutIndex)) {
      fetchPriceMapping(posLayoutDraft.stockList)
      setRecordedLayoutIndex(currentLayoutIndex)
    }
  }, [posLayoutDraft, isDeleting, recordedLayoutIndex, setRecordedLayoutIndex, currentLayoutIndex, fetchPriceMapping])

  const onDropIn = (e, id, itemIndex, item) => {
    preventDefault(e)
    // : e.dataTransfer.getData('text/price')'', 
    // '0', 
    // Number()
    const stockId = e.dataTransfer.getData('text/id') || item.stockId
    const priceString = e.dataTransfer.getData('text/price')
    const price = Number(priceString) || (priceString === '0' ? 0 : item.price)
    const newItem = {
      ...item,
      stockId,
      price,
      stockName: e.dataTransfer.getData('text/name') || item.stockName,
      backgroundColor: e.dataTransfer.getData('text/backgroundColor') || item.backgroundColor,
    }
    if (stockId) { updatePriceMapping({ [stockId]: price }) }
    editPosLayoutDraftItem({ id, index: itemIndex, item: newItem })
  }

  const onGridItemClick = index => {
    setCurrentLayoutItemIndex(index)
  }

  const onClearClick = (id, index) => {
    clearPosLayoutDraftItem({ id, index })
  }

  const onBackgroundColorChange = (e, id, index, item) => {
    const newItem = {
      ...item,
      backgroundColor: e.target.value,
    }
    editPosLayoutDraftItem({ id, index, item: newItem })
  }

  const onBackgroundColorInput = e => {
    editPreviousPalette(e.target.value)
  }

  const preventDefault = e => {
    e.preventDefault()
    e.stopPropagation()
    return false
  }

  const renderLayoutItems = () => {
    return (Object.keys(posLayoutDraft).length > 0) && posLayoutDraft.stockList.map((item, index) => {
      const selected = index === currentLayoutItemIndex ? 'selected' : ''
      return (
        <Grid
          item
          xs={4}
          style={{ backgroundColor: item.backgroundColor }}
          className={`grid-item ${selected}`}
          onDragEnter={preventDefault}
          onDragOver={preventDefault}
          onDrop={(e) => onDropIn(e, posLayoutDraft.id, index, item)}
          onClick={() => onGridItemClick(index)}
          key={index}
          data-item-index={index}
        >
          <div
            data-item-index={index}
            className="item-clear"
            onClick={() => onClearClick(posLayoutDraft.id, index)}>
            
          </div>

          <Typography variant="h4" data-item-index={index}>
            {item.stockName}
          </Typography>
          <Typography variant="subtitle1" data-item-index={index}>
            {priceMapping[item.stockId] || '-'}
          </Typography>
          {/* <p data-item-index={index}>{priceMapping[item.stockId] || '-'}</p> */}
          <div className="item-color" data-item-index={index}>
            <label data-item-index={index}>:&nbsp;</label>
            <input
              type="color"
              data-item-index={index}
              value={item.backgroundColor}
              onChange={(e) => onBackgroundColorChange(e, posLayoutDraft.id, index, item)}
              onBlur={e => onBackgroundColorInput(e)} />
          </div>
        </Grid>
      )
    })
  }

  return (
    <Box component="section" id="grid-input" sx={{
      position: 'relative',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      flexGrow: 1,
      backgroundColor: '#e0e0e0',
      '& .grid-field': {
        backgroundColor: '#cccccc',
        width: '83%',
        height: '90%',
        padding: 0,
      },
    }}>
      <div className="grid-field container-fluid">
        <Grid container sx={{
          height: '100%',
          borderBottom: '1px solid #999999',
          borderRight: '1px solid #999999',
          margin: 0,
          '& .grid-item': {
            position: 'relative',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            flexDirection: 'column',
            userSelect: 'none',
            borderTop: '1px solid #999999',
            borderLeft: '1px solid #999999',
            paddingTop: '0.5em',
            height: '20%',
            '&.selected': {
              boxShadow: '0px 0px 6px -3px #363636',
              border: '1px solid #33333377',
              borderTopWidth: '2px',
              borderLeftWidth: '2px',
              zIndex: 2,
            },
            '& h4': {
              fontSize: '1rem',
              fontWeight: 'bold',
              textAlign: 'center',
              marginBottom: 0,
              color: '#666666',
              letterSpacing: '0.025em',
              lineHeight: 1.5,
              textShadow: theme => `0px 1px 2px ${theme.palette.background.paper}`,
              overflow: 'overlay',
            },
          },
          '& .item-clear': {
            color: '#d6d6d666',
            position: 'absolute',
            right: '1%',
            top: '1%',
            width: '1.5em',
            lineHeight: '1.5em',
            fontSize: '0.8em',
            verticalAlign: 'middle',
            textAlign: 'center',
            cursor: 'pointer',
            '&:hover': {
              color: '#50505066',
            },
          },
          '& .item-color': {
            position: 'absolute',
            paddingRight: '5%',
            right: 0,
            bottom: 0,
            fontSize: '0.9rem',
            '& input': {
              border: 'none',
              borderRadius: '0.3em',
              padding: 0,
              width: '2.5em',
              height: '1.3em',
              userSelect: 'all',
            },
          },
        }}>
          {renderLayoutItems()}
        </Grid>
      </div>

      <div className="toggle-field"></div>
    </Box>
  )
}

const mapStateToProp = state => {
  return {
    posLayouts: state.posLayouts.content,
    posLayoutsStatus: state.posLayouts.loading,
    posLayoutDrafts: state.posLayoutDrafts,
    priceMapping: state.priceMapping.content,
  }
}

export default connect(
  mapStateToProp,
  { editPosLayoutDrafts, editPosLayoutDraftItem, clearPosLayoutDraftItem, editPreviousPalette, updatePriceMapping, fetchPriceMapping, fetchPosLayouts }
)(GridInput);