import React, { useState, useEffect, useRef } from 'react'
import { Box } from '@mui/material'
import { connect } from 'react-redux'
import { editPosLayoutDrafts, addPosLayoutDraft, deletePosLayoutDraft } from '../../slices/posLayoutDraftsSlice'
import { fetchPriceMapping } from '../../slices/priceMappingSlice'


const PlatTagList = ({
  isDeleting,
  setIsDeleting,
  posLayoutDrafts,
  currentLayoutIndex,
  setCurrentLayoutIndex,
  setRecordedLayoutIndex,
  setCurrentLayoutItemIndex,
  fetchPriceMapping,
  editPosLayoutDrafts,
  addPosLayoutDraft,
  deletePosLayoutDraft }) => {

  const [addOpen, setAddOpen] = useState(false)
  const [newTagName, setNewTagName] = useState('')
  const newTagRef = useRef()

  useEffect(() => {
    const onBodyClick = (event) => {
      if (newTagRef.current && newTagRef.current.contains(event.target)) return
      setAddOpen(false)
    }
    document.body.addEventListener('click', onBodyClick, { capture: true });
    return () => {
      document.body.removeEventListener('click', onBodyClick, { capture: true })
    }
  }, [])

  const getPriceMapping = (newIndex) => {
    fetchPriceMapping(posLayoutDrafts[newIndex].stockList)
    setRecordedLayoutIndex(newIndex)
  }

  const toggleAddTag = () => {
    setAddOpen(!addOpen)
  }

  const toggleDeleteTag = () => {
    setIsDeleting(!isDeleting)
  }

  const onTagClick = (e, index) => {
    if (e.target.classList.contains('delete-icon')) return
    setCurrentLayoutIndex(index)
    if (isDeleting) getPriceMapping(index)
    setCurrentLayoutItemIndex(0)
  }

  const onAddClick = () => {
    if (!newTagName) return;
    const newLayout = {
      id: getNewID(posLayoutDrafts),
      categoryName: newTagName
    };
    setCurrentLayoutIndex(posLayoutDrafts.length)
    addPosLayoutDraft(newLayout)
    setAddOpen(false)
    setNewTagName('')
  }

  const onDeleteClick = (id, index) => {
    if (posLayoutDrafts.length <= 1) return
    const isLast = currentLayoutIndex >= (posLayoutDrafts.length - 1)
    if (currentLayoutIndex > index || isLast) {
      setCurrentLayoutIndex(currentLayoutIndex - 1);
    }
    if (currentLayoutIndex === index) {
      if (isLast) {
        getPriceMapping(index - 1)
      } else {
        // currentLayoutIndex priceMapping 
        fetchPriceMapping(posLayoutDrafts[index + 1].stockList)
      }
    }
    deletePosLayoutDraft(id)
  }

  const onNameChange = e => {
    setNewTagName(e.target.value)
  }

  const getNewID = layouts => {
    let isRepeat = true
    let newID = ''
    const existingIDs = layouts.map(order => order.id);
    while (isRepeat) {
      newID = randomID()
      isRepeat = existingIDs.includes(newID)
    }
    return newID
  }

  const randomID = () => {
    return 'layout' + 'yxxx'.replace(/[xy]/g, function (c) {
      const r = Math.random() * 16 | 0
      const v = c === 'x' ? r : ((r & 0x3) | 0x8)
      return v.toString(16)
    })
  }

  const onDragStart = e => {
    e.dataTransfer.setData('text/index', e.target.dataset.tagIndex || '')
    e.dataTransfer.setData('text/layoutID', e.target.dataset.layoutId || '')
  }

  const onDragEnter = e => {
    e.currentTarget.classList.add('dragover')
    preventDefault(e)
  }

  const onDragLeave = e => {
    e.currentTarget.classList.remove('dragover')
    preventDefault(e)
  }

  const preventDefault = e => {
    e.preventDefault()
    e.stopPropagation()
    return false
  }


  const onDropIn = e => {
    const currentTarget = e.currentTarget
    currentTarget.classList.remove('dragover')
    if (isDeleting || !e.dataTransfer.getData('text/index') || !currentTarget.dataset.tagIndex) return
    const dragIndex = Number(e.dataTransfer.getData('text/index'))
    const dropBaseIndex = Number(currentTarget.dataset.tagIndex)
    // index"" currentLayoutIndex 
    //  GridInput  useEffect  fetchPriceMapping
    const dropIndex = dragIndex <= dropBaseIndex
      ? dropBaseIndex
      : dropBaseIndex + 1
    const slicedLayout = posLayoutDrafts.slice(dragIndex, dragIndex + 1)
    const newLayouts = posLayoutDrafts.filter((el, index) => index !== dragIndex)
    newLayouts.splice(dropIndex, 0, slicedLayout[0])
    editPosLayoutDrafts(newLayouts)
    setCurrentLayoutIndex(dropIndex)
  }

  const onTouchEnd = e => {
    const currentTarget = e.currentTarget
    const targetItem = document.elementFromPoint(e.changedTouches[0].clientX, e.changedTouches[0].clientY)
    if (isDeleting || !currentTarget.dataset.tagIndex || !targetItem.dataset.tagIndex) return
    const dragIndex = Number(currentTarget.dataset.tagIndex)
    const dropBaseIndex = Number(targetItem.dataset.tagIndex)
    // index"" currentLayoutIndex 
    //  GridInput  useEffect  fetchPriceMapping
    const dropIndex = dragIndex <= dropBaseIndex ? dropBaseIndex : dropBaseIndex + 1
    const slicedLayout = posLayoutDrafts.slice(dragIndex, dragIndex + 1)
    const newLayouts = posLayoutDrafts.filter((el, index) => index !== dragIndex)
    newLayouts.splice(dropIndex, 0, slicedLayout[0])
    editPosLayoutDrafts(newLayouts)
    setCurrentLayoutIndex(dropIndex)
  }

  const renderedTags = posLayoutDrafts.map((layout, index) => {
    const active = index === currentLayoutIndex ? 'active' : ''
    return (
      <div
        key={layout.id}
        draggable={!isDeleting}
        className={`plat-tag ${active}`}
        data-tag-index={index}
        data-layout-id={layout.id}
        onDragStart={onDragStart}
        onDragEnter={onDragEnter}
        onDragOver={preventDefault}
        onDragLeave={onDragLeave}
        onDrop={onDropIn}
        onTouchEnd={onTouchEnd}
        onClick={(e) => onTagClick(e, index)}
      >
        {layout.categoryName}
        <span className='delete-icon' onClick={() => onDeleteClick(layout.id, index)}></span>
      </div>
    )
  })

  return (
    <Box
      component="section"
      id="plat-tag-list"
      className={`${isDeleting ? 'deleting' : ''}`}
      sx={{
        '& .plat-tag': styles.platTagStyle,
        '& .delete-wrapper': styles.deleteWrapperStyle,
        '&.deleting': styles.deletingStyle,
      }}
    >
      <Box className="tag-wrapper" sx={{
        display: 'inline-block',
        width: 'calc(100% - 5em)',
        paddingTop: '0.5em',
      }}>
        {renderedTags}
        <div
          className={`plat-tag add-tag ${addOpen ? 'open' : ''}`}
          ref={newTagRef}>
          <input
            onChange={onNameChange}
            value={newTagName}
            className='new-tag-name'
            placeholder=""
            type="text" />
          <input
            onClick={onAddClick}
            className='new-tag-submit'
            type="button"
            value="" />
          <span className='add-icon' onClick={toggleAddTag}></span>
        </div>
      </Box>
      <div className="delete-wrapper">
        <div className="plat-tag" onClick={toggleDeleteTag}>
          {isDeleting ? '' : ''}
        </div>
      </div>
    </Box>
  )
}

const styles = {
  platTagStyle: {
    position: 'relative',
    backgroundColor: '#bbbbbb',
    display: 'inline-block',
    userSelect: 'none',
    cursor: 'pointer',
    padding: '0 1em',
    marginRight: '0.125em',
    borderTopLeftRadius: '0.4em',
    borderTopRightRadius: '0.4em',
    overflow: 'visible',
    transition: 'margin 0.3s ease-out',
    '&.dragover': {
      marginRight: '1.5em',
    },
    '&.dragover::after': {
      position: 'absolute',
      content: '""',
      right: 0,
      width: '1.6em',
      height: '1.5em',
      transform: 'translate(100 %, 0)',
    },
    '&.active': {
      color: '#666666',
      backgroundColor: 'background.default',
    },
    '& .new-tag-name': {
      maxWidth: 0,
      padding: 0,
      margin: 0,
      border: '0 solid #cccccc',
      display: 'inline-block',
      transition: 'all 0.3s linear',
      lineHeight: 1,
    },
    '& .new-tag-submit': {
      maxWidth: 0,
      padding: 0,
      margin: 0,
      border: '0 solid #cccccc',
      display: 'inline-block',
      transition: 'all 0.3s linear',
      lineHeight: 1.25,
      verticalAlign: 'top',
    },
    '& .add-icon': {
      display: 'inline-block',
      color: '#dddddd',
      backgroundColor: '#9d9d9d',
      verticalAlign: 'text-bottom',
      fontWeight: 'bold',
      textAlign: 'center',
      userSelect: 'none',
      fontSize: '0.8rem',
      borderRadius: '3em',
      lineHeight: 1.5,
      width: '1.5em',
      transform: 'rotate(45deg)',
      transition: 'all 0.2s linear',
    },
    '& .delete-icon': {
      display: 'none'
    },
    '&.open .new-tag-name': {
      maxWidth: '5em',
      padding: '0 0.25em',
      backgroundColor: '#f0f0f0',
    },
    '&.open .new-tag-submit': {
      maxWidth: '3em',
      padding: '0 0.25em',
      marginRight: '0.3em',
      border: '2px solid #b3b3b3',
      backgroundColor: '#e2e2e2',
      color: '#333333',
    },
    '&.open .add-icon': {
      transform: 'rotate(0deg)',
    },
  },
  deleteWrapperStyle: {
    display: 'inline-block',
    width: '5em',
    '& .plat-tag': {
      backgroundColor: '#cb5c5c',
      color: 'background.darkPaper',
      zIndex: 101,
    },
  },
  deletingStyle: {
    zIndex: 101,
    backgroundColor: '#fff1ed',
    '& .plat-tag': {
      color: '#444444',
      backgroundColor: '#ff8e6f',
    },
    '& .plat-tag.active': {
      color: 'background.paper',
      backgroundColor: '#fb6840',
    },
    '& .plat-tag .delete-icon': {
      display: 'inline-block',
      color: 'background.default',
      backgroundColor: '#fdc3a4',
      verticalAlign: 'text-bottom',
      fontWeight: 'bold',
      textAlign: 'center',
      userSelect: 'none',
      fontSize: '0.8rem',
      borderRadius: '3em',
      lineHeight: 1.5,
      width: '1.5em',
      transition: 'all 0.2s linear',
      marginLeft: '0.3em',
      transform: 'rotate(0deg)',
    },
    '& .plat-tag.add-tag': {
      display: 'none',
    },
    '& .delete-wrapper .plat-tag': {
      color: 'background.darkPaper',
      backgroundColor: '#524d4d',
    },
  },
}

const mapStateToProps = state => {
  return {
    posLayoutDrafts: state.posLayoutDrafts
  }
}

export default connect(
  mapStateToProps,
  { fetchPriceMapping, editPosLayoutDrafts, addPosLayoutDraft, deletePosLayoutDraft }
)(PlatTagList);